body {
    background: black;
    display: flex;
}

#call-container {
    display: block;
    width: 100vw;
    height: 100vh;
    overflow: hidden;
    font-family: "Emirates", "Emirates Medium", "Montserrat", "Arial", sans-serif;
    z-index: 0;
}

span.waiting-to-join {
    font-size: 30px;
    display: flex;
    align-items: center;
    justify-content: center;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 0;
}

span.waiting-to-join + video {
    z-index: 1;
}

span.waiting-to-join + .vega-embed {
    z-index: 3;
}

#call-container .stream {
    max-width: min(800px, calc(100% - 20px));
    border: 2px solid #ffffff22;
}